body {
  color: white;
  background-color: black;
  text-align: center;
}
.App {
  border: .4rem double white;
}
button:hover{
  background-color: gray;
}
button:focus{
  outline: 0;
  background-color: black;
  color: white;
}
a:hover{
  background-color: gray;
}
a:focus{
  background-color: black;
  color: white;
}
.childrenInlineBlock div{
    display: inline-block;
}
.soreTitle {
    margin-top: 2rem;
    margin-bottom: 2rem;
    border: .4em double white;
    /* border-radius: 50%; */
    display: inline-block;
}
.soreTitle button{
    font-family: Georgia, 'Times New Roman', Times, serif;
    border: .4rem double black;
    background-color: white;
    color: black;
    text-shadow: -.14em -.14em black;
    width: 100%;
    border-radius: 50%;
}
.leftForearmFistContainer {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    -webkit-transform-origin: 5rem;
            transform-origin: 5rem;
    -webkit-animation: leftFlexAnimation 10s infinite;
            animation: leftFlexAnimation 10s infinite;
}
.rightForearmFistContainer {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    -webkit-transform-origin: 0rem;
            transform-origin: 0rem;
    -webkit-animation: rightFlexAnimation 10s infinite;
            animation: rightFlexAnimation 10s infinite;
}

.leftBicep {
    width: 4rem;
    height: 4rem;
    background-color: white;
    border-top-left-radius: 50%;
    border-top-right-radius: 50%;
    border-bottom-left-radius: 20%;
    border-bottom-right-radius: 20%;
    -webkit-animation: leftBicepAnimation 10s infinite;
            animation: leftBicepAnimation 10s infinite;
}
.rightBicep {
    width: 4rem;
    height: 4rem;
    background-color: white;
    border-top-left-radius: 50%;
    border-top-right-radius: 50%;
    border-bottom-left-radius: 20%;
    border-bottom-right-radius: 20%;
    -webkit-animation: rightBicepAnimation 10s infinite;
            animation: rightBicepAnimation 10s infinite;
}
.leftForearm {
    width: 2rem;
    height: .7rem;
    background-color: white;
    border-top-left-radius: 20%;
    border-top-right-radius: 20%;
    border-bottom-left-radius: 20%;
    border-bottom-right-radius: 20%;
    -webkit-transform: matrix();
            transform: matrix()
}
.rightForearm {
    width: 2rem;
    height: .7rem;
    background-color: white;
    border-top-left-radius: 20%;
    border-top-right-radius: 30%;
    border-bottom-left-radius: 20%;
    border-bottom-right-radius: 30%;
}
.leftFist {
    width: 1rem;
    height: 1.4rem;
    background-color: black;
    border-radius: 50%;
    -webkit-transform: translateY(.1rem);
            transform: translateY(.1rem);
    border: .1rem solid white;
}
.rightFist {
    width: 1rem;
    height: 1.4rem;
    background-color: black;
    border-radius: 50%;
    -webkit-transform: translateY(.1rem);
            transform: translateY(.1rem);
    border: .1rem solid white;
}

@-webkit-keyframes leftFlexAnimation {
    0% {
        -webkit-transform: rotate(70deg);
                transform: rotate(70deg);
        -webkit-transform-origin: 3rem;
                transform-origin: 3rem;
    }
    50% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        -webkit-transform-origin: 3rem;
                transform-origin: 3rem;
    }
    100% {
        -webkit-transform: rotate(70deg);
                transform: rotate(70deg);
        -webkit-transform-origin: 3rem;
                transform-origin: 3rem;
    }
}

@keyframes leftFlexAnimation {
    0% {
        -webkit-transform: rotate(70deg);
                transform: rotate(70deg);
        -webkit-transform-origin: 3rem;
                transform-origin: 3rem;
    }
    50% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        -webkit-transform-origin: 3rem;
                transform-origin: 3rem;
    }
    100% {
        -webkit-transform: rotate(70deg);
                transform: rotate(70deg);
        -webkit-transform-origin: 3rem;
                transform-origin: 3rem;
    }
}
@-webkit-keyframes rightFlexAnimation {
    0% {
        -webkit-transform: rotate(-70deg);
                transform: rotate(-70deg);
        -webkit-transform-origin: 0rem;
                transform-origin: 0rem;
    }
    50% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        -webkit-transform-origin: 0rem;
                transform-origin: 0rem;
    }
    100% {
        -webkit-transform: rotate(-70deg);
                transform: rotate(-70deg);
        -webkit-transform-origin: 0rem;
                transform-origin: 0rem;
    }
}
@keyframes rightFlexAnimation {
    0% {
        -webkit-transform: rotate(-70deg);
                transform: rotate(-70deg);
        -webkit-transform-origin: 0rem;
                transform-origin: 0rem;
    }
    50% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        -webkit-transform-origin: 0rem;
                transform-origin: 0rem;
    }
    100% {
        -webkit-transform: rotate(-70deg);
                transform: rotate(-70deg);
        -webkit-transform-origin: 0rem;
                transform-origin: 0rem;
    }
}
@-webkit-keyframes leftBicepAnimation {
    0% {
        width: 1rem;
        height: 1rem;
    }
    50% {
        width: 2rem;
        height: 1rem;
    }
    100% {
        width: 1rem;
        height: 1rem;
    }
}
@keyframes leftBicepAnimation {
    0% {
        width: 1rem;
        height: 1rem;
    }
    50% {
        width: 2rem;
        height: 1rem;
    }
    100% {
        width: 1rem;
        height: 1rem;
    }
}
@-webkit-keyframes rightBicepAnimation {
    0% {
        width: 1rem;
        height: 1rem;
    }
    50% {
        width: 2rem;
        height: 1rem;
    }
    100% {
        width: 1rem;
        height: 1rem;
    }
}
@keyframes rightBicepAnimation {
    0% {
        width: 1rem;
        height: 1rem;
    }
    50% {
        width: 2rem;
        height: 1rem;
    }
    100% {
        width: 1rem;
        height: 1rem;
    }
}


@media only screen and (max-width: 500px){
    .soreTitle button{
        -webkit-animation: phoneLogoAnimation 10s infinite;
                animation: phoneLogoAnimation 10s infinite;
    }
    .leftForearmFistContainer {
        -webkit-animation: none;
                animation: none;
    }
    .rightForearmFistContainer {
        -webkit-animation: none;
                animation: none;
    }
    
    .leftBicep {
        width: 0rem;
        height: 0rem;
        -webkit-animation: none;
                animation: none;
    }
    .rightBicep {
        width: 0rem;
        height: 0rem;
        -webkit-animation: none;
                animation: none;
    }
    .leftForearm {
        width: 0rem;
        height: 0rem;
    }
    .rightForearm {
        width: 0rem;
        height: 0rem;
    }
    .leftFist {
        width: 0rem;
        height: 0rem;
        border: none;
    }
    .rightFist {
        width: 0rem;
        height: 0rem;
        border: none;
    }
    @-webkit-keyframes phoneLogoAnimation {
        0% {
            background-color: white;
        }
        50% {
            background-color: red;
        }
        100% {
            background-color: white;
        }
    }
    @keyframes phoneLogoAnimation {
        0% {
            background-color: white;
        }
        50% {
            background-color: red;
        }
        100% {
            background-color: white;
        }
    }
}
